import {createRouter,
  createWebHashHistory,createWebHistory
 } from 'vue-router'
import ying from '../views/ying.vue'
import ying01 from '../views/ying01.vue'
import dva from '../views/ganranti/dva.vue'
import dachui from '../views/ganranti/dachui.vue'
import xingxing from '../views/ganranti/xingxing.vue'
import pohuaiqiu from '../views/ganranti/pohuaiqiu.vue'
import xigema from '../views/ganranti/xigema.vue'

const routes = [
  {
    path:'/', 
    redirect:{name:"ying"},
 },
 {path:'/ying',name:"ying", component:ying},
 {path:'/ying01',component:ying01},
 {path:'/dva',component:dva},
 {path:'/dachui',component:dachui},
 {path:'/xingxing',component:xingxing},
 {path:'/pohuaiqiu',component:pohuaiqiu},
 {path:'/xigema',component:xigema},
]

const router = createRouter({
  history:createWebHashHistory(), //hash模式 ,还有history模式
  //如果想用history就把createWebHashHistory改成createWebHistory
  routes,
})

export default router
