import { createApp } from 'vue'
import App from './App.vue'
import ElementPlus from 'element-plus' //引入element
import 'element-plus/dist/index.css'  //引入element
import zhCn from 'element-plus/es/locale/lang/zh-cn' //element变成中文
import router from './router/index.js'
import * as ElementPlusIconsVue from '@element-plus/icons-vue' //引入图标库

const app = createApp(App)
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

app.use(ElementPlus, {locale: zhCn,}).
use(router).
mount('#app')

