<script setup>
import { ref, reactive, watch } from 'vue'
import { ElMessage } from 'element-plus'

const colors = [
    { color: '#f56c6c', percentage: 20 },
    { color: '#e6a23c', percentage: 40 },
    { color: '#5cb87a', percentage: 60 },
    { color: '#1989fa', percentage: 80 },
    { color: '#6f7ad3', percentage: 100 },
]

const dialogVisible = ref(false)
const jinengdianshu = ref(0)
const jindutiao = ref(0)
const jindutiao01 = ref(0)
const bianhuan = ref(false);



const manmanbianhua = reactive({
    shengji() {
        setTimeout(() => {   //设置延迟执行
            console.log(jindutiao01.value);
            jindutiao01.value -= 1;
            jindutiao.value += 3;
            if (jindutiao01.value > 0) {
                manmanbianhua.shengji();
            } else {
                bianhuan.value = false;
            }
            if (jindutiao.value >= 99) {
                jindutiao.value -= 99;
                jinengdianshu.value += 1;
            }
        }, 90-(jindutiao01.value*2)-(dengji.age*3));
    }
})

const dengji = reactive({
    age: 1,
    age01: 4,
    shengming: 3800,
    jichuyisu: 1.02,
    yisu: 127.5,
    shoujiyisu: 35,
    shiftjineng: "未获得额外效果",
    shengji: function () {
        if (dengji.age < 10) {
            jindutiao01.value += 11;
            dengji.age += 1;
            if (bianhuan.value == false) {
                manmanbianhua.shengji();
            }
            bianhuan.value = true;
            dengji.shengming += ((dengji.age * 7) + 70) * 2;
            dengji.jichuyisu += 0.006;
            dengji.yisu = 125 * dengji.jichuyisu;
            dengji.shoujiyisu -= 1.5;
            if (dengji.age == dengji.age01) {
                dengji.age01 += 3;
            }
        } else {
            dengji.zuigaoji();
        }
    },
    zuigaoji() {
        ElMessage({
            message: '已到达最大等级',
            grouping: true,
            type: 'error',
            duration: 1300
        })
    }
})
const suiji = reactive({
    shuzi: 0,
    fangchongfu: 0
})
const zhudongjineng = ref("技能点")
const zhudongjinengcss = reactive({
    fontSize: "1.7vw",
    color: "black",
    letterSpacing: "0"
})

const open2 = () => {
    suiji.shuzi = Math.floor(Math.random() * 3) + 1;
    if (suiji.shuzi == suiji.fangchongfu) {
        if (suiji.shuzi == 1) {
            suiji.shuzi = Math.floor(Math.random() * 2) + 2;
        } else if (suiji.shuzi == 2) {
            suiji.shuzi = Math.floor(Math.random() * 2) + 1;
            if (suiji.shuzi == 2) {
                suiji.shuzi = 3;
            }
        }
        else {
            suiji.shuzi = Math.floor(Math.random() * 2) + 1;
        }
    };
    if (suiji.shuzi == 1) {
        ElMessage({
            dangerouslyUseHTMLString: true,
            message: '<p style="color:#1E90FF">恭喜你抽到<b><虚空></b>主动技能</p>',
            type: 'success',
        })
        zhudongjineng.value = "虚空"
        zhudongjinengcss.color = "#1E90FF"
    } else if (suiji.shuzi == 2) {
        ElMessage({
            dangerouslyUseHTMLString: true,
            message: '<p style="color:#EE82EE">恭喜你抽到<b><幽魂></b>主动技能</p>',
            type: 'success',
        })
        zhudongjineng.value = "幽魂"
        zhudongjinengcss.color = "#EE82EE"
    } else {
        ElMessage({
            dangerouslyUseHTMLString: true,
            message: '<p style="color:#FF7F50">恭喜你抽到<b><爆破></b>主动技能</p>',
            type: 'success',
        })
        zhudongjineng.value = "爆破"
        zhudongjinengcss.color = "#FF7F50"
    }
    dialogVisible.value = false;
    zhudongjinengcss.fontSize = "2vw"
    zhudongjinengcss.letterSpacing = "0.3vw"
    suiji.fangchongfu = suiji.shuzi;
}

const value01 = ref(5);
const value02 = ref(1);
const value03 = ref(3);

</script>